//! moment.js locale configuration
//! locale : Japanese [ja]
//! author : LI Long : https://github.com/baryon

import moment from '../moment';

export default moment.defineLocale('ja', {
    eras: [
        {
            since: '2019-05-01',
            offset: 1,
            name: '',
            narrow: '',
            abbr: 'R',
        },
        {
            since: '1989-01-08',
            until: '2019-04-30',
            offset: 1,
            name: '',
            narrow: '',
            abbr: 'H',
        },
        {
            since: '1926-12-25',
            until: '1989-01-07',
            offset: 1,
            name: '',
            narrow: '',
            abbr: 'S',
        },
        {
            since: '1912-07-30',
            until: '1926-12-24',
            offset: 1,
            name: '',
            narrow: '',
            abbr: 'T',
        },
        {
            since: '1873-01-01',
            until: '1912-07-29',
            offset: 6,
            name: '',
            narrow: '',
            abbr: 'M',
        },
        {
            since: '0001-01-01',
            until: '1873-12-31',
            offset: 1,
            name: '',
            narrow: 'AD',
            abbr: 'AD',
        },
        {
            since: '0000-12-31',
            until: -Infinity,
            offset: 1,
            name: '',
            narrow: 'BC',
            abbr: 'BC',
        },
    ],
    eraYearOrdinalRegex: /(|\d+)/,
    eraYearOrdinalParse: function (input, match) {
        return match[1] === '' ? 1 : parseInt(match[1] || input, 10);
    },
    months: '1_2_3_4_5_6_7_8_9_10_11_12'.split('_'),
    monthsShort: '1_2_3_4_5_6_7_8_9_10_11_12'.split(
        '_'
    ),
    weekdays: '______'.split('_'),
    weekdaysShort: '______'.split('_'),
    weekdaysMin: '______'.split('_'),
    longDateFormat: {
        LT: 'HH:mm',
        LTS: 'HH:mm:ss',
        L: 'YYYY/MM/DD',
        LL: 'YYYYMD',
        LLL: 'YYYYMD HH:mm',
        LLLL: 'YYYYMD dddd HH:mm',
        l: 'YYYY/MM/DD',
        ll: 'YYYYMD',
        lll: 'YYYYMD HH:mm',
        llll: 'YYYYMD(ddd) HH:mm',
    },
    meridiemParse: /|/i,
    isPM: function (input) {
        return input === '';
    },
    meridiem: function (hour, minute, isLower) {
        if (hour < 12) {
            return '';
        } else {
            return '';
        }
    },
    calendar: {
        sameDay: '[] LT',
        nextDay: '[] LT',
        nextWeek: function (now) {
            if (now.week() !== this.week()) {
                return '[]dddd LT';
            } else {
                return 'dddd LT';
            }
        },
        lastDay: '[] LT',
        lastWeek: function (now) {
            if (this.week() !== now.week()) {
                return '[]dddd LT';
            } else {
                return 'dddd LT';
            }
        },
        sameElse: 'L',
    },
    dayOfMonthOrdinalParse: /\d{1,2}/,
    ordinal: function (number, period) {
        switch (period) {
            case 'y':
                return number === 1 ? '' : number + '';
            case 'd':
            case 'D':
            case 'DDD':
                return number + '';
            default:
                return number;
        }
    },
    relativeTime: {
        future: '%s',
        past: '%s',
        s: '',
        ss: '%d',
        m: '1',
        mm: '%d',
        h: '1',
        hh: '%d',
        d: '1',
        dd: '%d',
        M: '1',
        MM: '%d',
        y: '1',
        yy: '%d',
    },
});
